.Checkout {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  background-color: #eee;
  padding: 20rem;
}

.Close {
  color: #000;
  font-size: 36rem;
}

.MealsDesc {
  background-color: #fff;
  border-radius: 20px;
  padding: 0 20rem;
}

.Header {
  display: flex;
  border-bottom: 1px solid #f2f2f2;
  height: 80rem;
  align-items: center;
  font-size: 20rem;
}

.Meals {
  max-height: 1200rem;
  overflow: auto;
}

.Footer {
  position: relative;
  height: 120rem;
  border-top: 1px dashed #f2f2f2;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-weight: bold;
  font-size: 30rem;
}

.Footer::before,
.Footer::after {
  content: '';
  position: absolute;
  width: 22rem;
  height: 22rem;
  background-color: #eee;
  border-radius: 50%;
  top: -10rem;
  left: -30rem;
}

.Footer::after {
  left: auto;
  right: -30rem;
}

.TotalPrice::before {
  content: '合计:\00a0¥';
  font-weight: normal;
  font-size: 18rem;
  margin-right: 10rem;
}
